import React from 'react'
import { useDimension } from 'Page/StockEntry/GeneralServices/'
import CustomTable from 'Component/CustomTable'
import { activeStockTableHeader, data } from './services'

const { height, emptyItem, width, isMobile } = useDimension(90)
const customTableClass = isMobile ? ('stockMovement' === 'stockMovement' ? 'target-movement-mobile' : 'target-repack-mobile') : ('stockMovement' === 'stockMovement' ? 'target-movement' : 'target-repack')

const ActiveStock = () => {
    return (
        <div className="bg-white border radius-all w-100" style={{ minHeight: height, height: height, maxHeight: height }} >
            <CustomTable
                editColumn={'false'}
                className={`${customTableClass} `}
                styles={{ color: 'red', textAlign: 'right', width: '100%' }}
                data={data}
                columns={activeStockTableHeader} />
        </div >
    )
}

export default ActiveStock